<template>
  	<div v-cloak>
	    <ul class="resourcelist" v-if="inputsList.totalCount">
	      <li class="cropitem" v-for="item in inputsList.list">
	        <div class="time">{{item.inputDate | timeFormat(vm)}}</div>
	        <div class="userecord">
	          <div class="record">
	            <ul class="dailydose">
                <li class="input" v-for="ele in item.missionInputVOList">
                  <p class="name">{{ele.agriResName}}（{{ele.totalAmount | dosageFn(vm, ele.specUnit, ele.dose, ele.doseUnit)}}）</p>
                  <div class="info">
                    <span v-if="!simplify">类型：{{ele.agriCategoryName}}</span>
                    <span v-if="ele.productor">厂家：{{ele.productor}}</span>
                  </div>
                </li>
	            </ul>
	            <div class="bubble"></div>
	          </div>
	          <div class="dot"></div>
	        </div>
	      </li>
	    </ul>
	    <div class="nodata"  v-else-if="inputsList.hasOwnProperty('list')">
	      <img src="../../assets/images/no_inputs.png" alt="无数据"/>
	      <p>{{tips}}</p>
	    </div>
	    <div class="loading" v-else v-loading="true" element-loading-text="加载中..."></div>
  	</div>
</template>

<script>
export default {
  data () {
    return {
      vm: this
    };
  },
  props: ['simplify', 'inputsList', 'tips'],
  methods: {
    areaUnit: function (type) {
      let unit = null;
      switch (type) {
        case 1:
          unit = '亩';
          break;
        case 2:
          unit = '分';
          break;
        case 3:
          unit = '平方米';
          break;
        default:
          break;
      }
      return unit;
    }
  },
  filters: {
    timeFormat (time, currThis) {
      return currThis.$dateTimeUtil.ifDate(time, '.');
    },
    dosageFn (total, vm, specUnit, dose, doseUnit) {
      return vm.simplify ? total + specUnit : dose + specUnit + '/' + vm.areaUnit(doseUnit);
    }
  }
};
</script>

<style scoped lang="less">
  .flex(@x:center,@y:center) {
    display: flex;
    display: -webkit-flex;
    flex-flow: row nowrap;
    justify-content: @x;
    align-items: @y;
  }

  .fontS(@s:14px) {
    font-size: @s;
  }

  .textAlign(@align:center) {
    text-align: @align;
  }

  .resourcelist {
    padding: 20px 25px;

    .cropitem {
      .flex(flex-start, flex-start);

      .time {
        width: 115px;
        flex: none;
        color: #00F6FF;
        line-height: 22px;
        margin-top: -6px;
        padding-right: 12px;
        box-sizing: border-box;
        .fontS(16px);
        .textAlign(right);
      }

      .userecord {
        position: relative;
        flex: auto;
        border-left: 3px solid #19AEB3;
        padding: 2px 10px 14px 30px;

        .record {
          position: relative;
          background: #061F36;

          .dailydose {
            padding: 5px 25px 0;

            .input {
              padding: 10px 0;
              border-bottom: 1px solid #054F5B;
              line-height: 26px;
              .textAlign(left);

              .name {
                color: #A1EFF8;
                .fontS(16px);
              }

              .info {
                color: #6DC1CB;
                .fontS(14px);

                span:first-child {
                  margin-right: 16px;
                }
              }

              &:last-child {
                border-bottom: none;
              }
            }

          }

          .bubble {
            border: 6px solid transparent;
            border-top-color: #061F36;
            border-right-color: #061F36;
            transform-origin: center 3px;
            width: 0;
            left: 0;
            position: absolute;
            top: 0;
            left: -12px;
          }
        }

        .dot {
          position: absolute;
          top: 0;
          left: -6.5px;
          width: 10px;
          height: 10px;
          border-radius: 5px;
          background: #19AEB3;
        }
      }

      &:last-child .userecord {
        border-left-color: transparent;
      }
    }
  }

  .nodata {
    height: 460px;
    .flex();
    flex-flow: column;

    p {
      color: #6DC1CB;
      .fontS(20px);
      line-height: 42px;
    }
  }

	.loading /deep/ .el-loading-mask{background-color:rgba(0,0,0,0);padding-top:200px;}
</style>



